.title {
  max-width: 90%;

  margin-top: 0;
  margin-bottom: 0;
}

.description {
  max-width: 90%;
  word-break: break-word;
}

.buttonsBoxOperation {
  display: flex;
  flex-direction: row;
  column-gap: 25px;
}

.button {
  border: none;
  font: inherit;
  cursor: pointer;
  outline: none;
  border-radius: 4px;
  padding: 8px 12px;
  background-color: #3f51b5;
  color: #fff;
}

.button:hover,
.button:focus {
  background-color: #303f9f;
}

.button:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}
